// pages/index.js
// import React from 'react'
import {Component} from 'react'
import Layout from '../components/MyLayout.js'
import HeaderCategory from '../components/HeaderCategory.js';
// import workshops from '../data/WorkshopData.js'
import program from '../data/programDataTest';

class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    // this is where i Put my code?
    let testP = program;
    return (
      <Layout>
        <HeaderCategory />
        <div className="grid">
          <div className="col-1-1 floatLeft">
            <p>This is some frontpage content</p>
            <p>{program}</p>
          </div>
        </div>
        {program.map( function(ws, i) {
          return <WorkshopDescription
            key={ws.id}
            id={ws.id}
            shortUrl={ws.shortUrl}
            title={ws.title}
            lead={ws.lead}
            summary={ws.summary}
            // body={ws.body}
            // bio={ws.bio}
            info={ws.info}
            bgColor={ws.bgColor}
            // gallery
            category={ws.previous}
            // videolinks: 
            dates={ws.dates}
            year={ws.year}
            month={ws.month}
            dayName={ws.dayName}
            timeAndLocation={ws.timeAndLocation}    
            />
        })}
      </Layout>
    )
  }
}

